*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:hover {
  text-decoration: none;
  color: rgb(0, 0, 0);
}

a:focus, a:active {
  outline: none;
}

ul,
ol,
li {
  list-style: none;
}

button {
  cursor: pointer;
}

:focus, :active {
  outline: none;
}

.preload * {
  -webkit-transition: all 0.0001s ease-out;
  transition: all 0.0001s ease-out;
}

