body,
html {
  height: 100%;
  overflow-x: hidden;
  font-family: "Exo 2";
  line-height: 1;
  color: #2f3e46;
}

.container {
  max-width: 1180px;
  padding: 0 20px;
  margin: 0 auto;
  width: 100%;
}

.App {
  position: relative;
}

.main {
  margin-top: 10px;
}

.content {
  padding-left: 10px;
  flex: 0 0 75%;
}

.block {
  box-shadow: 0px 3px 8px #a3b18a;
  padding: 0 0 10px 0;
}

.title {
  font-size: 40px;
  color: #344e41;
  font-weight: 700;
  margin-bottom: 20px;
  margin-left: 10px;
}

.button {
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
  border: 0;
  padding: 8px 28px;
  background: #3a5a40;
  color: white !important;
  display: inline-block;
  transition: all 0.2s ease-in;
}
.button:hover {
  color: #fefae0 !important;
  background: #344e41;
}

.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #344e41;
  opacity: 0.95;
}

.modal {
  position: absolute;
  left: 50%;
  top: 50%;
  bottom: auto;
  right: auto;
  width: 50vw;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 10px;
  padding-bottom: 20px;
  opacity: 1 !important;
}
.modal__body {
  padding: 0 15px;
}
.modal__input {
  width: 100%;
  margin-bottom: 15px;
  padding: 8px 5px;
  border: 0;
  resize: none;
  border: 2px solid rgba(163, 177, 138, 0.5);
  border-bottom: 2px solid #588157;
  transition: all 0.2s ease-in;
}
.modal__input:invalid {
  border: 2px solid #e63946;
}
.modal__input:hover {
  border: 2px solid #588157;
}
.modal__input:focus {
  border: 2px solid #344e41;
}
.modal__button {
  float: right;
}
.modal__inputName {
  font-size: 28px;
  margin-bottom: 5px;
  font-weight: 600;
  color: #588157;
}

.row {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.ai-center {
  display: flex;
  align-items: center;
}

.hidden {
  display: none;
}

