body,
html{
  height: 100%;
  overflow-x: hidden;
  font-family: 'Exo 2';
  line-height: 1;
  color: map.get($colors, '_dark-gray');
}
html{

}

.container{
  max-width: 1180px;
  padding: 0 20px;
  margin: 0 auto;
  width: 100%;
}
.App{
  position: relative;
}
.main{
  margin-top: 10px;
}
.content{
  padding-left: 10px;
  flex: 0 0 75%;
}
.block{
  box-shadow: 0px 3px 8px map.get($colors, '_gray-green');
  padding:  0 0 10px 0;
}
.title{
  font-size: 40px;
  color: map.get($colors, '_dark-green');
  font-weight: 700;
  margin-bottom: 20px;
  margin-left: 10px;
}

.button{
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
  border: 0;
  padding: 8px 28px;
  background: map.get($colors, '_green');
  color:white !important;
  display: inline-block;
  transition: all 0.2s ease-in;
  &:hover{
    color: map.get($colors, '_milk') !important;
    background: map.get($colors, '_dark-green');
  }
}
.modalOverlay{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #344e41;
  opacity: 0.95;
}
.modal{
  position: absolute;
  left: 50%;
  top: 50%;
  bottom: auto;
  right: auto;
  width: 50vw;
  margin-right: -50%;
  transform: translate(-50%,-50%);
  background: white;
  padding: 10px;
  padding-bottom: 20px;
  opacity: 1 !important;
  &__body{
    padding: 0 15px;
  }
  &__input{
    width: 100%;
    margin-bottom: 15px;
    padding: 8px 5px;
    border: 0;
    resize: none;
    border: 2px solid rgba(163, 177, 138, 0.5); //gray green
    border-bottom: 2px solid map.get($colors, '_light-green');
    transition: all 0.2s ease-in;
    &:invalid{
      border: 2px solid map.get($colors, '_red');
    }
    &:hover{
      border: 2px solid map.get($colors, '_light-green');
    }
    &:focus{
      border: 2px solid map.get($colors, '_dark-green');
    }
  }
  &__button{
    float: right;
  }
  &__inputName{
    font-size: 28px;
    margin-bottom: 5px;
    font-weight: 600;
    color: map.get($colors, '_light-green');
  }
}

.row{
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.ai-center{
  display: flex;
  align-items: center;
}
.hidden{
  display: none;
}